.table {
	padding-bottom: 100px;
}
table {
	width: 100%;
	max-width: 700px;
	margin: 0 auto;
	margin-top: 100px;
	border-spacing: 2px;
	background-color: #ffffff;
}
.th-col {
	padding: 5px 10px;
	font-size: 14px;
	font-weight: normal;
	background-color: #f9f9f9;
	cursor: pointer;
	transition: 0.3s;
	&:hover {
		box-shadow: 0 0 2px 1px #898787;
	}
	&--small {
		width: 19%;
	}
	&--bg-blue {
		background-color: #edfaff;
	}
}
.td-col {
	width: 27%;
	height: 100%;
	height: 50px;
	text-align: center;
	padding: 5px 10px;
	background-color: #f9f9f9;
	cursor: pointer;
	&:hover {
		box-shadow: 0 0 2px 1px #898787;
	}
	&--1 {
		font-size: 14px;
	}
	&--small {
		width: 19%;
	}
	&--text-left {
		text-align: left;
	}
	&--bg-blue {
		background-color: #edfaff;
	}
	&--bg-green {
		background-color: #e0f6e1;
	}
	&--bg-red {
		background-color: #fed9d7;
	}
	&--float {
		span {
			display: inline-block;
			text-align: right;
		}
	}
	&__number {
		width: 50%;
		float: left;
	}
	&__per {
		float: right;
		width: 50%;
	}
	&__per--red {
		color: #e16571;
	}
	&__per--green {
		color: #5fb030;
	}
	&--graf {
		height: 250px;
		background-color: #fff;
	}
}