.plan-form {
	width: 100%;
	max-width: 400px;
	margin: 0 auto;
	margin-top: 100px;
	padding: 30px 10px;
	border: 8px solid #ebebeb;
	background-color: #f8f8f8;
	&__btn {
		min-width: 155px;
		margin-top: 15px;
		margin-left: 50%;
		transform: translateX(-50%);
		border-radius: 20px;
		background-color: #34a845;
		transition: 0.2s;
		&:hover,
		&:focus {
			background-color: #37c04b;
		}
	}
}
.plan-list {
	padding: 10px 0;
	border-bottom: 2px solid #efefef;
	&__item {
		position: relative;
		cursor: pointer;
	}
	&__label {
		position: relative;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 15px;
		width: 100%;
		padding-left: 45px;
		font-family: "Bebas", "Arial", sans-serif;
		font-size: 18px;
		font-weight: bold;
		color: #7b7f80;
		text-transform: uppercase;
		cursor: pointer;
		border: 1px solid transparent;
		transition: 0.2s;
		&:hover,
		&:focus {
			background-color: #ebf5f7;
		}
		&::before {
			position: absolute;
			content: '';
			left: 10px;
			top: 50%;
			transform: translateY(-50%);
			width: 15px;
			height: 15px;
			border: 4px solid #ebebeb;
			border-radius: 50%;
		}
		&--active {
			background-color: #ebf5f7;
			border: 1px solid #cbdadf;
			&::before {
				position: absolute;
				content: '';
				left: 10px;
				top: 50%;
				transform: translateY(-50%);
				width: 15px;
				height: 15px;
				border: 4px solid #496b87;
				border-radius: 50%;
			}
			&::after {
				position: absolute;
				content: '';
				left: 18px;
				top: 50%;
				transform: translateY(-50%);
				width: 7px;
				height: 7px;
				background-color: #0294be;
				border-radius: 50%;
			}
		}
		
	}
	&__input {
		display: none;
		font-size: 0;
		max-width: 60%;
		margin-right: 2%;
	}
	&__price {
		max-width: 38%;
		font-family: "Arial", sans-serif;
		font-size: 14px;
		font-weight: normal;
		text-transform: lowercase;
	}
}
//------counter---------///
.count-license {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	padding: 20px;
	border-bottom: 2px solid #efefef;
	&__label {
		font-size: 14px;
		font-weight: normal;
		color: #201f1f;
	}
	&__select {
		min-width: 55px;
		margin-left: 10px;
		padding: 5px 0;
		font-size: 13px;
		font-weight: bold;
		color: #4d4a4a;
		border: 2px solid #9cb5de;
		border-radius: 3px;
		box-shadow: 0 0 3px #9cb5de;
	}
}
.total-price {
	display: flex;
	justify-content: center;
	margin-top: 20px;
	font-family: "Bebas", "Arial", sans-serif;
	font-weight: bold;
	font-size: 26px;
	&__text {
		position: relative;
		padding-right: 10px;
		text-transform: uppercase;
		color: #7b7f80;
		&::before {
			position: absolute;
			content: ':';
			bottom: 0;
			right: 5px;
		}
	}
	&__number {
		position: relative;
		padding-right: 20px;
		color: #4085a1;
		&::after {
			position: absolute;
			content: 'us';
			top: 3px;
			right: 5px;
			font-size: 17px;
			font-weight: bold;
			color: #4085a1;
		}
	}
}
.select-plan {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 30px;
	font-size: 14px;
	color:#0f98c8;
	&__text {
		padding-right: 7px;
	}
}